import React from "react";
import { Link, Route, Switch } from "react-router-dom";
import Home from "./pages/Home";
import Favorite from "./pages/Favorite";
import Library from "./pages/Library";
import LogIn from "./pages/LogIn";
import { LoggedProvider } from "./components/LoggedContext";
import './styles/nav.css'
import './styles/top.css'
import './styles/poster.css'
import './styles/input.css'
import './styles/profile.css'

const links = {
  Library: '/library',
  Favorite: '/favorite',
  Home: '/'
}

const Navigation = () => {
  return (
    <LoggedProvider>
      <header className="nav">
        <div className="head-wrapper header-height">
          <div className="title-wrapper center header-height"> <h2>Naxocist</h2> </div>
          {Object.entries(links).map(([name, path], i) => <Link key={i} className='nav-link center header-height' to={path}> {name} </Link>)}
        </div>
      </header>

      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/favorite" component={Favorite} />
        <Route path="/library" component={Library} />
        <Route path="/login" component={LogIn} />
      </Switch>


    </LoggedProvider>
  );
}

export default Navigation;