import React, { useState } from 'react'
import { Redirect } from 'react-router-dom'
import { LoggedContext } from '../components/LoggedContext'

const LogIn = () => {
  const [invalid, setInvalid] = useState('')

  const { loggedIn, setLoggedIn } = React.useContext(LoggedContext);

  const submit = (e) => {
    e.preventDefault();

    const { password } = e.target;
    const adminPass = "animesimper"

    if (password.value === adminPass) {
      setLoggedIn(true);
      sessionStorage.setItem("loggedIn", "true");
    } else {
      setInvalid('Invalid password!')
    }
  }

  if (loggedIn) {
    return <Redirect to="/library" />
  }

  return (
    <React.Fragment>
      <form onSubmit={submit}>
        <h1>Login</h1>
        <div className="credentials">
          <div className="c-wrap">

            <label>Admin Pass</label>
            <input type="password" name="password" placeholder="Admin pass . . ." />
            <button className="submit-btn" type="submit">Submit</button>
            <p>{invalid}</p>
            <img alt="akko_gif" src="https://i.pinimg.com/originals/99/f3/f2/99f3f24cdd4bfd47052d2cfa771b2d8a.gif" />
          </div>
        </div>
      </form>
    </React.Fragment>
  )

}

export default LogIn;