import React, { useState, useEffect } from "react";
import RotateLoader from "react-spinners/RotateLoader";
import Top from '../components/Top'
import Search from '../components/Search'
import Profile from '../components/Profile'
import Input from '../components/Input'

const axios = require('axios');

const Home = () => {
	const [topAnime, setTopAnime] = useState([]);
	const [topManga, setTopManga] = useState([]);
	const [results, setResults] = useState([]);
	const [loading, setLoading] = useState(false);
	const [notFound, setNotFound] = useState('')

	const search = (keyword) => {
		fetch(`https://api.jikan.moe/v3/search/anime?q=${keyword}&page=1`)
			.then(res => res.json())
			.then(data => {
				if (data.results) {
					setResults(data.results);
					setNotFound('')
					window.scrollTo(0, 2200);
				} else {
					setNotFound('Invalid Keyword!')
				}
			});
	}


	useEffect(() => {
		setLoading(true);
		axios.get(`https://api.jikan.moe/v3/top/anime/1/airing`)
			.then(res => {
				setTopAnime(res.data.top.slice(0, 5));
			})
		axios.get('https://api.jikan.moe/v3/top/manga/1')
			.then(res => {
				setTopManga(res.data.top.slice(0, 5));
				setLoading(false);
			})
	}, []);


	return (
		<div className="panel">
			<header className="top-header">Profile</header>
			<Profile />
			{loading ? (
				<div className="anime-wrapper">
					<div className="loader">
						<RotateLoader color={"#3895d3"} loading={loading} size={10} />
					</div>
				</div>
			) : (
				<React.Fragment>

					<header className="top-header">Top airing anime</header>
					<div className="anime-wrapper">
						{topAnime.map((a, i) => <Top anime={a} key={i} />)}
					</div>

					<header className="top-header">Top Manga</header>
					<div className="anime-wrapper">
						{topManga.map((a, i) => <Top anime={a} key={i} />)}
					</div>

					<div className="input">
						<p>{notFound}</p>
						<Input callback={search} placeholder="Search anime . . ." />
					</div>

				</React.Fragment>
			)}

			<div className="anime-wrapper">
				{results.map((a, i) => <Search anime={a} key={i} />)}
			</div>

		</div>
	);
}

export default Home
